const app = getApp()
var touchStartX = 0; 
var touchStartY = 0;  
var touchMoveX = 0; 
var touchMoveY = 0; 

export default {
	data() {
		return {
			url: "/static/cardback0.jpg",
			fold: false,
			sponsor: {},
			activitylist: [],
			activityindex: 0,
			activitymore: true,
			momentlist: [],
			momentindex: 0,
			momentmore: true,
			current: 0,
			tabs: [
				"活动", "动态"
			],
		};
	},
	onLoad(option) {
		console.log(option)
		this.sponsor.name = option.id
		if (app.globalData.cookie != '') {
			console.log('onload')
			this.loadpage()
		} else {
			console.log('nocookie')
			app.globalData.cookieReadyCallback = this.loadpage
		}
		uni.showShareMenu({})
	},
	onShareAppMessage(res) {
		console.log(res)
		return {
			title: '清易票-' + this.sponsor.name,
			//imageUrl: app.globalData.shareimg
		}
	},
	methods: {
		loadpage() {
			this.momentindex = 0
			this.momentlist = []
			this.momentmore = true
			this.activityindex = 0
			this.activitylist = []
			this.activitymore = true
			uni.request({
				url: app.globalData.apiurl + 'sponsors/view/' + this.sponsor.name,
				header: {
					'content-type': 'application/json', 
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(res)
					this.sponsor = res.data
				}
			})
			if (this.current == 0) {
				this.loadactivity()
			} else {
				this.loadmoment()
			}
		},
		loadactivity(e) {
			console.log(e)
			if (this.activitymore) {
				uni.request({
					url: app.globalData.apiurl + 'users/events',
					data: {
						index: this.activityindex,
						sponsor_name: this.sponsor.name,
					},
					header: {
						'content-type': 'application/json', 
						'cookie': app.globalData.cookie
					},
					success: (res) => {
						console.log(res)
						res.data.events.forEach((item, index) => {
							item.delay = '' + (index + 1) * 0.1 + 's'
							if (!item.img_url || (item.img_url == '')) {
								item.img_url = app.globalData.backimg[parseInt('11' + item.event_id) % 4]
							}
							setTimeout(() => {
								item.delay = undefined
							}, (index + 11) * 100)
						})
						this.activitylist = this.activitylist.concat(res.data.events)
						this.activitymore = res.data.more
						this.activityindex += res.data.events.length
						if (this.activityindex == 0) {
							this.activityindex = -1
						}
					}
				})
			}
		},
		loadmoment(e) {
			console.log(e)
			if (this.momentmore) {
				console.log('loadmoment' + this.momentindex)
				uni.request({
					url: app.globalData.apiurl + 'users/moments',
					data: {
						'index': this.momentindex,
						'sponsor_name': this.sponsor.name
					},
					header: {
						'content-type': 'application/json',
						'cookie': app.globalData.cookie
					},
					success: (res) => {
						console.log(res)
						res.data.moments.forEach((item, index) => {
							item.sponsor_avatar = this.sponsor.avatar_url
							item.delay = '' + (index + 1) * 0.1 + 's'
							setTimeout(() => {
								item.delay = undefined
							}, (index + 11) * 100)
						})
						this.momentlist = this.momentlist.concat(res.data.moments)
						this.momentmore = res.data.more
						this.momentindex += res.data.moments.length
						if (this.momentindex == 0) {
							this.momentindex = -1
						}
					}
				})
			}
		},

		touchStart: function(e) {
			touchStartX = e.touches[0].pageX;
			touchStartY = e.touches[0].pageY; 
		},
		touchMove: function(e) {
			touchMoveX = e.touches[0].pageX;
			touchMoveY = e.touches[0].pageY;
			if (touchMoveY - touchStartY <= -30) {
				console.log("向上滑动")
				touchStartY = e.touches[0].pageY
				this.fold = true
				console.log(this.fold)
			} else if (touchMoveY - touchStartY >= 30) {
				console.log('向下滑动 ')
				touchStartY = e.touches[0].pageY
				this.fold = false
				console.log(this.fold)
			}
		},
		tabSelect(e) {
			this.current = e.currentTarget.dataset.id;
		},
		navChange(index) {
			this.current = index;
		},
		swiperChange(e) {
			this.current = e.detail.current;
			if (this.current == 0 && this.activityindex != -1) {
				this.loadactivity()
			} else if (this.momentindex != -1) {
				this.loadmoment()
			}
		},
		share() {

		},
		follow() {
			uni.request({
				url: app.globalData.apiurl + 'users/follow/' + this.sponsor.name,
				method: 'POST',
				header: {
					'content-type': 'application/json', 
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(res.data);
					this.sponsor.follow = res.data.follow
				}
			})
		},
		appreciate(index) {
			
		},
		activityPage(id) {
			var page = getCurrentPages()
			page = page[page.length - 2]
			if (page.route == 'pages/activity/activity' && page.options.id == id) {
				uni.navigateBack()
			} else {
				uni.navigateTo({
					url: "../activity/activity?id=" + id
				})
			}
		},
		like(index) {
			//console.log(id)
			uni.request({
				url: app.globalData.apiurl + 'users/like/' + this.activitylist[index].event_id, 
				method: 'POST',
				header: {
					'content-type': 'application/json', 
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(res.data);
					this.activitylist[index].like = res.data.like
				}
			})
		}
	}
}
